@charset "UTF-8";
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: "Poppins", sans-serif;
}

body {
  width: 100%;
  height: 100%;
  overflow-x: hidden !important;
}

main {
  overflow-x: clip !important;
}

body,
html {
  scroll-behavior: smooth;
  scroll-padding-top: 80px;
  font-size: 62.5%;
}

.container {
  margin: 0 auto;
  width: 100%;
  max-width: 1500px;
  padding: 0em 3em;
}

a {
  text-decoration: none;
  color: #000;
}

ul {
  list-style-type: none;
}

.header {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 70dvh;
  background-image: url("../../dist/img/header_small.png");
  background-size: cover;
  background-position: center;
  z-index: 1;
  overflow: hidden;
}
.header::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(20, 23, 25, 0.98);
  z-index: -1;
}
.header__box-heading {
  line-height: 1.3;
  color: #edf2ff;
  font-size: clamp(2.5rem, 2.5rem + 3.2037vw, 8rem);
}
.header__box-heading span {
  display: inline-block;
  position: relative;
  width: max-content;
  padding: 0.3em 0.2em;
  z-index: 1;
}
.header__box-heading span::after {
  position: absolute;
  content: "";
  top: 50%;
  left: 0;
  animation: xd 1s forwards;
  height: 60%;
  transform: translate(0, -50%);
  border-radius: 17px;
  background-color: #fdb802;
  z-index: -1;
}
.header__box-text {
  font-size: clamp(1.6rem, 1.2308rem + 1.0256vw, 2rem);
  color: #dcdcdc;
  font-weight: 200;
}
.header__buttons {
  display: flex;
  gap: 3em;
  justify-content: center;
  margin-top: 5em;
  flex-direction: column;
}
.header__buttons-button {
  text-align: center;
  padding: 0.5em 3em;
  color: #fff;
  font-size: clamp(1.8rem, 0.8769rem + 2.5641vw, 2.4rem);
  border-radius: 15px;
  font-weight: 600;
}
.header__buttons-button--first {
  background-color: #fdb802;
}
.header__buttons-button--second {
  border: 2px solid #fff;
}
.header__arrow {
  position: absolute;
  bottom: 10em;
  z-index: 1;
  animation: upAndDown 1s infinite;
}
.header__arrow img {
  height: 40px;
}

@keyframes upAndDown {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(20px);
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes xd {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}
.our-services .section__box {
  padding: 0 2em;
}
.our-services .container {
  padding: 0;
}

@media (max-width: 768px) {
  .our-services {
    overflow-x: hidden;
  }
}
.service__box {
  display: flex !important;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  padding: 4em 6em;
  text-align: center;
  gap: 2em !important;
}
.service__box-image {
  background: #ffb800;
  padding: 4em;
  border-radius: 50%;
  margin-bottom: 2em;
}
.service__box-image img {
  height: 50px;
  width: auto;
}
.service__box-heading {
  font-size: clamp(2rem, 1.2rem + 2.2222vw, 2.8rem);
  font-weight: 700;
}
.service__box-text {
  font-size: clamp(1.6rem, 1.3333rem + 0.7407vw, 1.8rem);
  font-weight: 300;
  color: #1d1d1d;
  letter-spacing: -0.5px;
}
.service__box-button {
  font-size: clamp(1.6rem, 1.0667rem + 1.4815vw, 2.2rem);
  padding: 0.6em 1.2em;
  margin-top: 3em;
  font-weight: 600;
  color: #fff;
  background-color: #1a2024;
  border-radius: 13px;
}

.section-padding {
  padding: 6vh 0;
}

.section__box {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: center;
  color: #131618;
}
.section__box-heading {
  font-size: clamp(2.8rem, 1.4667rem + 3.7037vw, 4.2rem);
}
.section__box-text {
  font-size: clamp(1.6rem, 1.0667rem + 1.4815vw, 2.2rem);
}

.swiper {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 100%;
  clip-path: inset(0 -200px 0 0);
  overflow: visible !important;
  margin-left: auto;
  padding: 10em 2em 10em 7em !important;
}
.swiper::before, .swiper::after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  width: 40px; /* Szerokość gradientu */
  z-index: 10;
  pointer-events: none;
}
.swiper::before {
  left: -2px;
  background: linear-gradient(to right, rgb(255, 255, 255), rgba(255, 255, 255, 0));
}
.swiper-pagination {
  display: none;
  position: relative !important;
  justify-content: center;
  bottom: 0 !important;
  padding: 2em;
  align-items: center;
}
.swiper-pagination-bullet {
  transition: transform 0.1s;
}
.swiper-pagination-bullet-active {
  background-color: #ffb800 !important;
  transform: scale(1.5) !important;
}
.swiper-container {
  overflow: visible !important;
  position: relative;
}
.swiper-button-prev, .swiper-button-next {
  display: none !important;
  border-radius: 50%;
  position: relative !important;
  margin: 5em 0;
  left: 0 !important;
  top: 0 !important;
  box-shadow: 0px 4px 3.7px rgba(0, 0, 0, 0.09);
}
.swiper-button-prev {
  background-color: #ffb800;
  color: #fff !important;
  padding: min(3vh + 0.8vw, 44px) !important;
}
.swiper-button-prev::after {
  content: "" !important;
}
.swiper-button-next {
  background-color: #fff;
  color: #ffb800 !important;
  padding: min(2vh + 0.8vw, 30px) !important;
}
.swiper-button-next::after {
  content: "" !important;
}
.swiper-slide {
  border-radius: 50px;
  height: 100%;
  width: auto;
  transition: opacity 0.5s ease;
  transition: transform 0.6s ease, opacity 0.5s ease;
  box-shadow: 0px 0px 56px -15px rgba(0, 0, 0, 0.15);
}
.swiper-slide.active-slide {
  z-index: 1000;
}

.swiper-container {
  display: flex !important;
  flex-direction: row !important;
  justify-content: center;
  align-items: center;
}

.swiper-pagination {
  display: flex;
}

.swiper-buittons {
  position: absolute;
  left: -20em;
  top: 35%;
  transform: translate(0, -35%);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  z-index: 1000 !important;
}

.swiper-scrollbar {
  display: none;
}

@media (max-width: 992px) {
  .swiper-buittons {
    display: none;
  }
}
@media (max-width: 768px) {
  .header__arrow {
    display: none;
  }
  .swiper {
    padding: 10em 3em !important;
  }
  .swiper::after, .swiper::before {
    width: 0;
  }
}
@media (min-width: 576px) {
  .container {
    padding: 0 5em !important;
  }
  .section__box {
    text-align: left;
  }
  .swiper-slide {
    transform: scale(0.92) !important;
  }
  .swiper-slide.active-slide {
    transform: scale(1.1) !important;
    z-index: 1000;
  }
}
@media (min-width: 768px) {
  .header__box {
    text-align: center;
  }
  .header__box-heading span {
    transform: rotate(-3deg);
  }
  .header__buttons {
    flex-direction: row;
  }
}
@media (min-width: 992px) {
  .container {
    padding: 0 15em;
  }
  .header {
    background-image: url("../../dist/img/header_big.png");
  }
}
@media (min-width: 1200px) {
  .swiper-button-prev,
  .swiper-button-next {
    display: flex !important; /* Ukryj przyciski */
  }
  .swiper-buittons {
    left: -15em;
  }
  .our-services .container {
    padding: 0 20em;
  }
}
@media (min-width: 1400px) {
  .swiper-buittons {
    left: -18em;
  }
}